import { useState } from "react";
import { Field, Formik, Form } from "formik";
import { InputTextField } from "./InputTextField";
import { DropDownField } from "./DropDownField";
import * as Yup from "yup";
import MobileDatePicker from "./MobileDatePicker";
import {
  TabsContent,
} from "../common/components/ui/tabs"
import { Button } from "../common/components/ui/button"
import { Label } from "../common/components/ui/label"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "../common/components/ui/card"
const ShiftForm = (props) => {
  // const [pickerKey, setPickerKey] = useState(0);
  const FormValidationSchema = Yup.object({
    companyID: Yup.string().required("Company name is required"),
    shiftDate: Yup.string().required("Shift date is required"),
    startTime: Yup.string().required("Start time is required"),
    endTime: Yup.string().required("End time is required"),
  });

  const getTimeDifference = (shiftDate, startTime, endTime) => {
    const shiftDate_date = shiftDate.split("T")[0]
    const startDate = new Date(`${shiftDate_date} ${startTime}`);
    const endDate = new Date(`${shiftDate_date} ${endTime}`);

    // If the shift ends on the next day, add 1 day to the end date
    if (endDate < startDate) {
      endDate.setDate(endDate.getDate() + 1);
    }

    const timeDifferenceInMilliseconds = endDate - startDate;
    const hours = Math.floor(timeDifferenceInMilliseconds / (1000 * 60 * 60));
    const minutes = Math.floor(
      (timeDifferenceInMilliseconds % (1000 * 60 * 60)) / (1000 * 60)
    );

    return `${hours}:${minutes < 10 ? "0" : ""}${minutes}`;
  };

  const timeToDecimal = (time) => {
    const [hours, minutes] = time?.split(":").map(Number);
    return hours + minutes / 60;
  };

  const getShiftTime = (shiftDate, start, end) => {
    const timeDifference = getTimeDifference(shiftDate, start, end);
    const timeArray = timeDifference.split(":");
    //return `You worked for ${timeArray[0]} hours and ${timeArray[1]} minutes`;
    return `You have worked for a total of  ${timeArray[0]} hours and ${timeArray[1]} minutes during this shift.`;
  };
  const [formKey, setFormKey] = useState(0);

  return (

    <TabsContent value="all">
      <Card x-chunk="dashboard-06-chunk-0">
        <CardHeader>
          <CardTitle>{props.label}</CardTitle>
          <CardDescription>
            {props.label == "Add Shift" ? 'Please enter below details to add shift' : "Please edit below details to update shift"}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Formik
            key={formKey}
            initialValues={props.initialState}
            validationSchema={FormValidationSchema}
            onSubmit={async (values, { setFieldValue, setValues, setSubmitting, resetForm }) => {
              try {
                const timeDifference = getTimeDifference(
                  values.shiftDate,
                  values.startTime,
                  values.endTime
                );
                const decTime = await timeToDecimal(timeDifference);
                const data = { ...values, totalHours: decTime };
                await props.handleSubmit(data);
                setSubmitting(false);
                setFieldValue("totalHours", "");
                setFormKey((prevKey) => prevKey + 1);
              } catch (error) {
                console.error(error);
                setSubmitting(false); // Ensure that form submission is stopped on error
              }
            }}
          >
            {({ values, setFieldValue, touched, errors }) => {
              console.log(values)
              return (
                <Form>
                  <div className="grid gap-6">
                    <div className="grid gap-3">
                      <Label >Company Name</Label>
                      <Field
                        label="Company Name"
                        setFormikFieldvalue={setFieldValue}
                        options={props?.companies}
                        placeholder="Select Company"
                        required
                        name={`companyID`}
                        value={`${values.companyID}`}
                        component={DropDownField}
                      />
                    </div>
                    <div className="grid gap-3">
                      <Label htmlFor="name">Work Location</Label>
                      <Field
                        name={`location`}
                        value={`${values.location}`}
                        label="Work Location"
                        required
                        placeholder="Work Location"
                        component={InputTextField}
                      />
                    </div>
                    <div className="grid gap-3">
                      <Label htmlFor="name">Shift Date</Label>
                      <Field
                        currvalue={`${values.shiftDate}`}
                        setFormikFieldvalue={setFieldValue}
                        inputFormat="MM/dd/yyyy"
                        name={`shiftDate`}
                        required
                        placeholder="Pick Date"
                        label="Shift Date"
                        component={MobileDatePicker}
                      />
                      {/* <MobileDatePicker
                            currvalue={`${values.shiftDate}`}
                            setFormikFieldvalue={setFieldValue}
                            inputFormat="MM/dd/yyyy"
                            name={`shiftDate`}
                            required
                            placeholder="Pick Date"
                            label="Shift Date"
                          /> */}
                    </div>
                    <div className="grid gap-6 sm:grid-cols-2">
                      <div className="grid gap-3">
                        <Label htmlFor="name">Start Time</Label>
                        <Field
                          name={`startTime`}
                          value={`${values.startTime}`}
                          required
                          label="Start Time"
                          type="time"
                          component={InputTextField}
                        />
                      </div>
                      <div className="grid gap-3">
                        <Label htmlFor="name">End Time</Label>
                        <Field
                          name={`endTime`}
                          required
                          value={`${values.endTime}`}
                          label="End Time"
                          type="time"
                          component={InputTextField}
                        />
                      </div>
                    </div>
                    <div className="flex flex-col items-center gap-1 text-center">
                      <p className="text-sm text-muted-foreground">
                        {values?.endTime && values?.startTime &&
                          getShiftTime(
                            values.shiftDate,
                            values.startTime,
                            values.endTime
                          )
                        }
                      </p>
                    </div>

                    <div className="flex flex-col items-center  text-center">
                      <Button className="mt-4" type="submit"> {props.label}</Button>
                    </div>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </CardContent>
      </Card>
    </TabsContent>

  );
};

export default ShiftForm;
