import React, { useState, useEffect } from "react";
import {
    Select,
    SelectContent,
    SelectGroup,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "./components/ui/select"
export const DropDownField = ({ form, field, options, placeholder,
    setFormikFieldvalue,
    currvalue }) => {
    const { name, value } = field;
    // console.log(name,value)
    const [isPopoverOpen, setIsPopoverOpen] = useState(false);
    const handleChange = (newValue) => {
        setFormikFieldvalue(name, newValue);
        setIsPopoverOpen(false);
    };
  
    return (
        <Select defaultValue={value} open={isPopoverOpen} onOpenChange={setIsPopoverOpen} onValueChange={handleChange}>
            <SelectTrigger >
                <SelectValue placeholder={placeholder} />
            </SelectTrigger>
            <SelectContent>
                <SelectGroup>
                    {options?.map((data, index) => (
                        <SelectItem key={index} value={data?._id}>{data?.part_time_name}</SelectItem>

                    ))}
                </SelectGroup>
            </SelectContent>
        </Select>
    );
};
