import React from "react";
import { Input } from "./components/ui/input"

export const InputTextField = ({ form, field, ...props }) => {
    const { name, value = '' } = field;
    const { handleChange, handleBlur } = form;
    const { label, required, multiline, placeholder, type, size = "small", fullWidth = true, helperText, error, className, disabled, inputProps } = props;

    return (
        <Input
            id={name}
            name={name}
            value={value}
            label={label}
            error={!!error}
            className={className}
            helperText={error ? error : helperText}
            required={required}
            multiline={multiline}
            rows={multiline && 3}
            placeholder={placeholder}
            type={type}
            fullWidth={fullWidth}
            disabled={disabled}
            variant="outlined"
            onChange={handleChange}
            onBlur={handleBlur}
            InputProps={inputProps}
        />
    );
};
