import axios from "axios";
import { serverAddress } from "../../constants.js";

const api = {
  addnewParttime: (params) => {
    if (params !== undefined) {
      let url = serverAddress + "/api/part/add";
      return axios.post(url, params);
    }
  },
  getallParttime: (params) => {
    if (params !== undefined) {
      let url = serverAddress + `/api/part/all/${params.userId}`;
      return axios.get(url);
    }
  },
  updateParttime: (params) => {
    if (params !== undefined) {
      let url = serverAddress + `/api/part/update/${params.id}`;
      return axios.put(url, params);
    }
  },
};

export default api;
