import axios from "axios";
import { serverAddress } from "../../constants.js";

const api = {
  addnewShift: (params) => {
    if (params !== undefined) {
      let url = serverAddress + "/api/time/add";
      return axios.post(url, params);
    }
  },
  updateShift: (params) => {
    if (params !== undefined) {
      let url = serverAddress + `/api/time/update/${params?.id}`;
      return axios.put(url, params);
    }
  },
  deleteShift: (params) => {
    if (params !== undefined) {
      let url = serverAddress + `/api/time/delete/${params?._id}`;
      return axios.delete(url, params);
    }
  },
  getallShifts: (params) => {
    if (params !== undefined) {
      let url =
        serverAddress +
        `/api/time/all/${params?.userId}?page=${params?.page}&limit=${params?.limit
        }${params?.parttime_id ? `&parttime_id=${params?.parttime_id}` : ""}${params?.fromDate ? `&startDate=${params?.fromDate}` : ""
        }${params?.toDate ? `&endDate=${params?.toDate}` : ""}`;
      return axios.get(url);
    }
  },
  downloadShiftData: (params) => {
    if (params !== undefined) {
      let url =
        serverAddress +
        `/api/time/generate-pdf/${params?.userId}?page=${params?.page}&limit=${params?.limit
        }${params?.parttime_id ? `&parttime_id=${params?.parttime_id}` : ""}${params?.fromDate ? `&startDate=${params?.fromDate}` : ""
        }${params?.toDate ? `&endDate=${params?.toDate}` : ""}${`&companyFilter=${params?.companyFilter}`}`;
      return axios.get(url, {
        responseType: 'blob',
      });
    }

  }
};

export default api;
