import * as types from "../../constants";
import parttimeAPI from "../../utils/api/parttimeAPI";

export function addnewParttime(params) {
  return async (dispatch) => {
    dispatch({
      type: types.CREATE_NEW_PARTTIME_PENDING, // Dispatch the pending action
    });
    try {
      const response = await parttimeAPI.addnewParttime(params);
      dispatch({
        type: types.CREATE_NEW_PARTTIME_FULFILLED,
        payload: response.data,
      });
      return response.data;
    } catch (error) {
      dispatch({
        type: types.CREATE_NEW_PARTTIME_REJECTED,
        payload: error.message,
      });
      throw error;
    }
  };
}

