import * as types from "../../constants";
import shiftAPI from "../../utils/api/shiftAPI";

export function addnewShift(params) {
  return async (dispatch) => {
    dispatch({
      type: types.CREATE_NEW_SHIFT_PENDING, // Dispatch the pending action
    });
    try {
      const response = await shiftAPI.addnewShift(params);
      dispatch({
        type: types.CREATE_NEW_SHIFT_FULFILLED,
        payload: response.data,
      });
      return response.data;
    } catch (error) {
      dispatch({
        type: types.CREATE_NEW_SHIFT_REJECTED,
        payload: error.message,
      });
      throw error;
    }
  };
}

