export function downloadShifts(params) {
  return async () => {
    try {
      // console.log("download called", params)
      const response = await shiftAPI.downloadShiftData(params);
      console.log("download called", response)
      const url = window.URL.createObjectURL(new Blob([response.data]));

      // Create a temporary anchor element to trigger the download
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'file.pdf'); // Set the filename for the download
      document.body.appendChild(link);
      link.click();

      // Clean up
      link.parentNode.removeChild(link);
    } catch (error) {
      throw error;
    }
  };
}