import { React, useState } from "react"
import { format } from "date-fns"
import { Calendar as CalendarIcon } from "lucide-react"
import { formatDate } from "./Formators";
import { cn } from "./lib/utils"
import { Button } from "./components/ui/button"
import { Calendar } from "./components/ui/calendar"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "./components/ui/popover"

export default function DatePickerDemo({ form, field,
  setFormikFieldvalue,
  currvalue,
  views = ["year", "month", "day"],
}) {
  const [date, setDate] = useState(currvalue ? new Date(currvalue) : null)
  const [isPopoverOpen, setIsPopoverOpen] = useState(false);
  const { name, value = '' } = field;
  const handleChange = (newValue) => {
    const d = formatDate(newValue);
    let date = `${d[3]}-01-${d[2]}`;
    if (views.includes("day")) {
      date = `${d[3]}-${d[0]}-${d[2]}`;
    }
    setDate(newValue);
    setFormikFieldvalue(name, date);
    setIsPopoverOpen(false);
  };


  return (
    <Popover open={isPopoverOpen} onOpenChange={setIsPopoverOpen}>
      <PopoverTrigger asChild>
        <Button
          variant={"outline"}
          onClick={() => setIsPopoverOpen(true)}
          className={cn(
            "justify-start text-left font-normal",
            !date && "text-muted-foreground"
          )}
        >
          <CalendarIcon className="mr-2 h-4 w-4" />
          {date ? format(date, "PPP") : <span>Pick the shift date</span>}
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-auto p-0">
        <Calendar
          mode="single"
          selected={date}
          onSelect={handleChange}
          disabled={(date) =>
            date > new Date() || date < new Date("2020-01-01")
          }
          initialFocus
        />
      </PopoverContent>
    </Popover>
  )
}
